import React, { Component } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import Aux from "../../hoc/Auxiliary/Auxiliary";

import Header from "../../components/general/Header/Header";
import Home from "./Home/Home";
import Register from "./Register/Register";
import Cart from "./Cart/Cart";
import ProductDetails from "./ProductDetails/ProductDetails";
import SearchProducts from "./SearchProducts/SearchProducts";
import Checkout from "./Checkout/Checkout";
import Logout from "./Register/Logout/Logout";
import Profile from "./Profile/Profile";
import MyOrders from "./Profile/MyOrders/MyOrders";

// ^ ADMIN
import Admin from "./Admin/Admin";
import AdminProducts from "./Admin/AdminProducts/AdminProducts";
import AdminProductDetails from "./Admin/AdminProductDetails/AdminProductDetails";
import Users from "./Admin/Users/Users";
import Orders from "./Admin/Orders/Orders";
import OrderDetails from "./OrderDetails/OrderDetails";
import EditUser from "./Admin/Users/EditUser/EditUser";

// ^ REDUX
import { connect } from "react-redux";

class Layout extends Component {
  render() {
    // ^ ########### GUARDING ROUTES ##############
    let routes = null;

    if (this.props.isRegistered) {
      // ^ ROUTES FOR ADMINS ONLY
      if (this.props.userRole === "admin") {
        routes = (
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/cart/:id" element={<Cart />} />
            <Route path="/cart" element={<Cart />} />
            <Route path="/checkout/*" element={<Checkout />} />
            <Route path="/product/:id" element={<ProductDetails />} />
            <Route path="/search/:keyword" element={<SearchProducts />} />
            <Route path="/search/" element={<Home />} />
            <Route path="/myOrders" element={<MyOrders />} />
            <Route path="/myOrders/:id" element={<OrderDetails />} />

            {/* Authentication */}
            <Route path="/signout" element={<Logout />} />
            <Route path='/profile/*' element={<Profile />} />

            {/* Admin */}
            <Route path="/admin" element={<Admin />} />
            <Route path="/admin/products_list/:page" element={<AdminProducts />} />
            <Route
              path="/admin/products_list/product/:id/edit"
              element={<AdminProductDetails action="edit" />}
            />
            <Route
              path="/admin/products_list/product/add"
              element={<AdminProductDetails action="add" />}
            />
            <Route path="/admin/users_list/:page" element={<Users />} />
            <Route path="/admin/users_list/user/:id" element={<EditUser />} />
            <Route path="/admin/orders_list/:page" element={<Orders />} />
            <Route path="/admin/orders_list/order/:id" element={<OrderDetails />} />

            {/* REDIRECT FOR DISALLOWED OR UNDEFINED ROUTES */}
            <Route path="*" element={<Navigate to='/' />} />
          </Routes>
        );
      } else {
        routes = (
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/cart/:id" element={<Cart />} />
            <Route path="/cart" element={<Cart />} />
            <Route path="/checkout/*" element={<Checkout />} />
            <Route path="/product/:id" element={<ProductDetails />} />
            <Route path="/search/:keyword" element={<SearchProducts />} />
            <Route path="/search/" element={<Home />} />
            <Route path="/myOrders" element={<MyOrders />} />
            <Route path="/myOrders/:id" element={<OrderDetails />} />

            {/* Authentication */}
            <Route path="/signout" element={<Logout />} />
            <Route path='/profile/*' element={<Profile />} />

            {/* REDIRECT FOR DISALLOWED OR UNDEFINED ROUTES */}
            <Route path="*" element={<Navigate to='/' />} />
          </Routes>
        );
      }
    } else if (!this.props.isRegistered) {
      routes = (
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/cart/:id" element={<Cart />} />
          <Route path="/cart" element={<Cart />} />
          <Route path="/product/:id" element={<ProductDetails />} />
          <Route path="/search/:keyword" element={<SearchProducts />} />
          <Route path="/search/" element={<Home />} />

          {/* Authentication */}
          <Route path="/register/*" element={<Register />} />

          {/* REDIRECT FOR DISALLOWED OR UNDEFINED ROUTES */}
          <Route path="*" element={<Navigate to='/' />} />
        </Routes>
      );
    }
    // ^ ##########################################

    return (
      <Aux>
        <Header
          scrolled={this.props.headerScrolled}
          isRegistered={this.props.isRegistered}
          userRole={this.props.userRole}
        />

        {/* ######  ROUTES  ##### */}
        {routes}
        {/* ##################### */}
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  return {
    isRegistered: state.register.token != null,
    userRole: state.register.userRole,
  };
};

export default connect(mapStateToProps)(Layout);
